// creating is markup
const gallery = document.querySelector('.gallery');
const markingString = images
  .map(
    ({ preview, original, description }) => `
<li class="gallery-item">
  <a class="gallery-link" width='1112px' height = '640px' href="${original}">
    <img
      class="gallery-image"
      src="${preview}"
      alt="${description}"
    />
  </a>
</li>
`
  )
  .join('');
// add markup to HTML
gallery.insertAdjacentHTML('afterbegin', markingString);

new SimpleLightbox('.gallery a', {
  sourceAttr: 'href',
  overlay: true,
  nav: true,
  captions: true,
  captionsData: 'alt',
  captionDelay: 250,
});
